<template>
  <div class="container">
    <div class="bg" :bg="collections[this.id].bg"></div>
    <CollectionText :collection="this.id"></CollectionText>
    <div @click="openSlideshow" class="collection-img" :src="collections[this.id].frontImage.url"></div>
    <!-- <img class="ladder" src="/svg/ladder-white.svg" alt=""> -->
  </div>
</template>

<script>
import json from '../data/collections.json'
import CollectionText from '../components/CollectionText.vue'
import setColorsMixin from '../mixins/setCollectionColorsMixin.js'
export default {
  data() {
    return {
      collections: json,
      id: this.$route.params.id
    }
  },
  mixins: [setColorsMixin],
  components: {
    CollectionText
  },
  mounted() {
    this.init();
    this.setColors(this.collections[this.id].overviewColor);
  },
  methods: {
    init() {
      this.slideshow = false;
      $("#nav-wrap").css('top', '158%');
      $(document).ready(function(){
        $(".bg").css('background-image', 'url('+ $(".bg").attr('bg') + ')');
        $('.collection-img').css('background-image', 'url('+$('.collection-img').attr('src')+')');
        $('.bg').css('opacity', '1');
        $('.text').addClass('text-enter');
        $('.collection-img').css('opacity', '1');
        $('.collection-img').css('right', '0');
      });
    },
    leave() {
      $('.bg').css('opacity', '0');
      $('.text').addClass('text-leave');
      $('.text').removeClass('text-enter');
      $('.collection-img').css('opacity', '0');
      $('.collection-img').css('right', '-50%');
    },
    openSlideshow() {
      this.$router.push({ name: 'collection-detail', params: { id: this.id } })
    }
  },
  watch: {
    $route (to, from) {
      this.id = this.$route.params.id;
      this.init();
    }
  },
  beforeRouteUpdate(to, from, next) {
    this.leave();
    setTimeout(function () {
      next();
    }, 300);
  },
  beforeRouteLeave(to, from, next) {
    this.leave();
    setTimeout(function() {
      next();
    }, 300)
  }
}
</script>

<style scoped>

  .text-leave {
    opacity: 0;
    top: -50px;
  }

  .text-enter {
    opacity: 1;
    top: 0;
  }

  .bg {
    position: fixed;
    z-index: -1;
    top: 0;
    left: 0;
    width: 100vw;
    height: 100vh;
    background-size:cover;
    background-repeat: no-repeat;
    transition: opacity .3s ease;
    opacity: 1;
  }

  .ladder {
    position: fixed;
    height: calc(100vh + 8px);
    right: -300px;
  }

  .container {
    display: grid;
    grid-template-columns: 50% 50%;
    grid-template-rows: auto;
    grid-template-areas: "left right";
    height: 100vh;
    padding-left: 50px;
  }

  .images {
    width: 45vw;
    grid-area: right;
    position: relative;
    transition: clip-path .5s ease, transform .5s ease;
    cursor: pointer;
    clip-path: polygon(25% 0%, 100% 0%, 100% 100%, 0% 100%);
    -webkit-clip-path: polygon(25% 0%, 100% 0%, 100% 100%, 0% 100%);
  }

  .collection-img {
    position: absolute;
    cursor: pointer;
    background-size: 100%;
    background-repeat: no-repeat;
    background-position: center;
    transition: right .3s ease, opacity .3s ease, background-size .3s ease;
    -moz-transition: right .3s ease, opacity .3s ease, background-size .3s ease;
    -ms-transition: right .3s ease, opacity .3s ease, background-size .3s ease;
    -o-transition: right .3s ease, opacity .3s ease, background-size .3s ease;
    -webkit-transition: right .3s ease, opacity .3s ease, background-size .3s ease;
    height: 100vh;
    width: 45vw;
    top: 0;
    right: -50%;
    clip-path: polygon(25% 0%, 100% 0%, 100% 100%, 0% 100%);
    -webkit-clip-path: polygon(25% 0%, 100% 0%, 100% 100%, 0% 100%);
  }

  .collection-img:hover {
    background-size: 110%;
  }

</style>
