<template lang="html">
  <div id="slideshow" class="container">
    <div class="slides-wrapper">
        <div class="slide" v-for="slide in collections[this.id].slides" :id="slide.id">
          <div class="slide-img" :class="img.orientation" v-for="img in slide.images" :src="img.url"></div>
        </div>
    </div>
    <div class="slides-overlay">
      <div class="overlay-bottom">
        <img @click="prevSlide" class="slide-control arrow-left" src="/svg/arrow-left.svg" alt="">
        <h1 id="count">{{currentCount}}</h1>
        <img @click="nextSlide" class="slide-control arrow-right" src="/svg/arrow-right.svg" alt="">
      </div>
      <div class="overlay-top">
        <router-link :to="{ name: 'collections', params: {id: this.id} }"><img class="close-btn" src="/svg/close-btn.svg" alt=""></router-link>
        <h1>{{collections[this.id].title}}</h1>
      </div>
    </div>
  </div>
</template>

<script>
import json from '../data/collections.json'
import setColorsMixin from '../mixins/setCollectionColorsMixin.js'
export default {
  data() {
    return {
      id: this.$route.params.id,
      collections: json,
      currentCount: 1
    }
  },
  mixins: [setColorsMixin],
  mounted() {
    $('.slide-img').animate({
      opacity: 1
    }, 300);
    $("#nav-wrap").css('top', '158%');
    this.setIcons(this.collections[this.id].slideshowColor);
    var wrapperWidth = $('.slide').length * 100;
    $('.slides-wrapper').css('width', wrapperWidth + '%');
    $('.slide:first-child').addClass('active-slide');
    this.setColors(this.collections[this.id].slideshowColor);
    this.setImages();
  },
  methods: {
    setImages() {
      $('.slide-img').each(function(i){
        var path = $(this).attr('src');
        $(this).css('background-image', 'url('+path+')');
      });
    },
    nextSlide() {
      var current = "";
      $('.slide').each(function(i){
        if ($(this).hasClass('active-slide')) {
          current = i;
          $(this).removeClass('active-slide');
        }
      });
      var next = current + 1;
      var count = $('.slide').length;
      if (next == count) {
        next = 0;
      }
      var amount = '0%';
      this.animateCounter(next+1);
      this.animateSlide(current, next, amount);

    },
    prevSlide() {
      var current = "";
      $('.slide').each(function(i){
        if ($(this).hasClass('active-slide')) {
          current = i;
          $(this).removeClass('active-slide');
        }
      });
      var next = current - 1;
      var count = $('.slide').length;
      if (next < 0) {
        next = count - 1;
      }
      var amount = '0%';
      this.animateCounter(next+1);
      this.animateSlide(current, next, amount);
    },
    animateSlide(current, next, amount) {
      var currentSlide = $('#slide-' + current);
      var nextSlide = $('#slide-' + next);
      var count = $('.slide').length;
      nextSlide.addClass('active-slide');
      $('.slide').each(function(i){
          var id = i;
          var left = 100 / count * next;
          $(this).animate({
            left: '-' + left + '%'
          }, 1000);
      });
    },
    animateCounter(count) {
      var vm = this;
      $('#count').animate({
        opacity: 0
      }, function(){
        vm.currentCount = count;
        console.log(vm.currentCounter);
        $('#count').animate({
          opacity: 1
        })
      })
    },
    setIcons(color) {
      $('.arrow-left').attr('src', '/svg/arrow-left-' + color + '.svg');
      $('.arrow-right').attr('src', '/svg/arrow-right-' + color + '.svg');
      $('.close-btn').attr('src', '/svg/close-btn-' + color + '.svg');
    },
    leave() {
      $('.slide-img').animate({
        opacity: 0
      }, 300);
    }
  },
  beforeRouteUpdate(to, from, next) {
    this.leave();
    setTimeout(function () {
      next();
    }, 300);
  },
  beforeRouteLeave(to, from, next) {
    this.leave();
    setTimeout(function() {
      next();
    }, 300)
  }
}
</script>

<style scoped>
h1 {
    font-size: 114px;
    margin: 0px;
    line-height: 90px;
    color: white;
}

.slides-wrapper {
  display: flex;
}

#slideshow {
  position: fixed;
  height: 100vh;
  width: 100vw;
  left: 0;
}

.overlay-top {
  position: fixed;
  left: 278px;
  top: 0px;
  display: flex;
  justify-content: space-between;
  align-items: center;
  width: calc(100vw - 278px);
  z-index: 2;
}

.overlay-top>h1 {
  margin-right: 20px;
}

.overlay-bottom {
  position: fixed;
  left: 278px;
  bottom: 20px;
  display: flex;
  justify-content: space-between;
  align-items: center;
  width: calc(100vw - 278px);
  z-index: 2;
}

.close-btn {
  transition: transform .3s ease;
}

.close-btn:hover{
  transform: rotate(90deg)
}

.arrow-right, .arrow-left, .close-btn {
  cursor: pointer;
}

.arrow-right {
  margin-right: 20px;
}

.slide {
  display: flex;
  width: 100%;
  position: relative;
}
.slide-img {
  height: 100vh;
  background-repeat: no-repeat;
  background-position: center;
  opacity: 0;
}

.slide-img.portrait {
  width: 33.333333333333%;
  background-size: cover;
  transition: background-size .4s ease;
}

.slide-img.landscape {
  width: 100%;
  background-size: cover;
  transition: background-size .4s ease;
}

</style>
